.appointment {
  padding: 38px 29px 49px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    padding: 61px 134px 58px;
  }

  @media (min-width: $desktop-width) {
    padding: 61px 300px 58px;
  }
}

.appointment__title {
  font-weight: 700;
  font-size: 21px;
  line-height: 1.24;
  text-align: center;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 0.87;
    margin: 0 auto 44px;
  }
}

.appointment__text {
  color: $black-lighter;
  text-align: center;
  text-transform: uppercase;
  margin: 0 0 32px;

  @media (min-width: $tablet-width) {
    margin: 0 auto 41px;
  }
}

.appointment__link {
  color: $white;
  background-color: $blue;
  padding: 16px 10px;
  display: flex;
  justify-content: center;
  width: 284px;
  font-weight: 700;
  font-size: 21px;
  line-height: 1.24;
  text-transform: uppercase;
  text-decoration: none;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 458px;
    margin: 0 auto;
  }
}
