.header {
  background-color: $white;
  min-height: 56px;
}

.header__logo {
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;

  &--hide {
    display: none;
  }
}

.header-wrapper {
  @media (min-width: $tablet-width) {
    padding: 0 28px 0 49px;
  }
}
